@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Roboto&family=Special+Elite&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
   --light-text: white;
   --light-text-2: #e04242;
   --light-gray:lightgray;

   /* white */
   --white:#ffffff;
   --white-1:#f5f5f5;
   --white-2:#ebebeb;
   --white-3:#e0e0e0;
   --white-4:#d6d6d6;
   --white-5:#cccccc;

   --black:black;
 
   --bg-color-1:#0b520b;
   --bg-color-2:#333333e6;
   --bg-color-3:#becf21;
   --bg-color-4:#0b213b80;
   --bg-color-5:#0b520b;
 
    --color-1:#7290be;
    --color-2:#cecece;
    --color-3:#f5f5f5;
    --color-4:#333;
    --color-5: #85796f;
    --color-6:#808080;
 
   --font-1: 'Caveat', cursive;
   --font-2:'Roboto', sans-serif;
   --font-3:'Special Elite', cursive;
   --font-4:'Times New Roman', Times,serif;
 
   --ancient-1:#e9d8a6;
   --ancient-2:#ee9b00;
   --ancient-3:#94d2bd;
   --ancient-4:#0a9396;
   --ancient-4:#005f73;

   /* bluish */
   --neutral-0:#48cae4;
   --neutral-1:#00b4d8;
   --neutral-2:#0096c7;
   --neutral-3:#0077b6;
   --neutral-4:#023e8a;
   --neutral-5:#03045e;

    /* burlywood */
    --br-1:#ede0d4;
    --br-2:#e6ccb2;
    --br-3:#ddb892;
    --br-4:#b08968;
    --br-5:#7f5539;
    --br-6:#9c6644;

    /* red */
    --rd-1:#e85d08;
    --rd-2:#dc2f02;
    --rd-3:#d00000;
    --rd-4:#9d0208;
    --rd-5:#6a040f;
    --rd-6:#370617;

    /* green */
    --gr-1:#004b23;
    --gr-2:#006400;
    --gr-3:#007200;
    --gr-4:#008000;
    --gr-5:#38b000;
    --gr-6:#70e000;

    /* melon */
    --ml-1:#e8a598;
    --ml-2:#ffb5a7;
    --ml-3:#fec5bb;
    --ml-4:#fcd5ce;
    --ml-5:#fae1dd;
    --ml-6:#f8edeb;

    /* yellow */
    --yellow-1:#ffea00;
    --yellow-2:#ffdd00;
    --yellow-3:#ffd000;
    --yellow-4:#ffc300;
    --yellow-5:#ffb700;
    --yellow-6:#ffaa00;

}

body{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ddb892;
  background-color: var(--br-3);
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-2);
}

header{
  font-size: 40px;
}
header,
footer{
  background-color:#b08968;
  background-color:var(--br-4);
  padding: 30px;
  text-align: center;
}

footer{
  bottom: 0;
  width: 100%;
}
.footerWrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

footer p{ 
  font-family: 'Roboto', sans-serif; 
  font-family: var(--font-2);
  font-size: 1.5rem;
  padding: 20px;
}

footer .icons i {
  color:#ddb892;
  color:var(--br-3);
  margin: 20px ;
  font-size: 30px;
}



footer .icons i:hover{
  color: #ee9b00;
  color: var(--ancient-2);
  transition: 1s;
}
.footerWrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

footer p{ 
  font-family: 'Roboto', sans-serif; 
  font-family: var(--font-2);
  font-size: 1.5rem;
  padding: 20px;
}

footer .icons i {
  color:#ddb892;
  color:var(--br-3);
  margin: 20px ;
  font-size: 30px;
}



footer .icons i:hover{
  color: #ee9b00;
  color: var(--ancient-2);
  transition: 1s;
}

h1{
  padding-left: 10px;
}

main{
  background-color:#e6ccb2;
  background-color:var(--br-2);
  display: block;
  justify-content: center;
  text-align: center;
  padding: 2rem 2rem;
  margin: 20px;
  margin-top: 50px;
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-2);
}

.circle{
  width: 150px;
  height: 150px;
  margin-bottom: 40px;
  border-radius: 50%;
  background-color:#ddb892;
  background-color:var(--br-3);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 50px;
  font-weight: 700;
}

button{
  width: 250px;
  height: 60px;
  margin: 20px;
  background-color: #7f5539;
  background-color: var(--br-5);
  color: black;
  color: var(--black);
  border: none;
  border-radius: 5px;
  padding: 1rem;
  cursor: pointer;
  font-size: 30px;
  font-weight: 700;
  justify-content: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-2);
}


@media screen and (max-width:568px) {
  html,body{
    overflow-x: hidden;
  }

  header{
    font-size: 10px;
  }

  button{
    width: 150px;
    height: 50px;
    margin: 10px;
    font-size: 20px;
    font-weight: 400;

  }
  .circle{
    width: 100px;
    height: 100px;
}

main{
 padding:10px 0;
  margin: 0;
  margin-top: 30px; 
}

footer p{ 
  font-size: 1rem;
  padding: 20px;
}

footer .icons i {
  margin: 10px ;
  font-size: 20px;
}
header,
footer{ 
  padding: 30px;
}

}

@media screen and (min-width:569px) and (max-width:768px) {
  html,body{
    overflow-x: hidden;
  }
  header{
    font-size: 40px;
  }

  main{
    margin: 0;
    margin-top: 30px;
    margin-bottom: 150px;
    display: block;
    flex-wrap: wrap;
  }
   
   footer p{ 
     font-size: 2rem;
     padding: 30px;
   }
   
   footer .icons i {
     margin: 30px ;
     font-size: 40px;
   }
 
   button{
    width: 200px;
    height: 60px;
    margin: 20px;
    font-size: 20px;
    font-weight: 600;
  
  }
}

